import { ADD_USER, DELETE_USER, EDIT_USER, SET_USERS, SET_USERS_SEARCH_VALUE, } from "../constants";
const transformServerUserToUser = (serverUser) => (Object.assign(Object.assign({}, serverUser), { is_active: serverUser.is_active === "1" }));
const reducer = (state, action) => {
    switch (action.type) {
        case SET_USERS: {
            const serverUsers = action.payload;
            const users = serverUsers.map(transformServerUserToUser);
            return Object.assign(Object.assign({}, state), { users });
        }
        case ADD_USER: {
            const serverUser = action.payload;
            const user = transformServerUserToUser(serverUser);
            return Object.assign(Object.assign({}, state), { users: [...state.users, user] });
        }
        case EDIT_USER: {
            const editedServerUser = action.payload;
            const editedUser = transformServerUserToUser(editedServerUser);
            const users = state.users.map((user) => user.id === editedUser.id ? editedUser : user);
            return Object.assign(Object.assign({}, state), { users });
        }
        case DELETE_USER: {
            const userId = action.payload;
            const users = state.users.filter((user) => user.id !== userId);
            return Object.assign(Object.assign({}, state), { users });
        }
        case SET_USERS_SEARCH_VALUE: {
            const usersSearchValue = action.payload;
            return Object.assign(Object.assign({}, state), { usersSearchValue });
        }
        default:
            return state;
    }
};
export { reducer };
