import { OPEN_NEW_TASK_MODAL, OPEN_EDIT_TASK_MODAL, CLOSE_TASK_MODAL, OPEN_NEW_STAGE_MODAL, CLOSE_STAGE_MODAL, OPEN_STAGE_EDIT_MODAL, CLOSE_PIPELINE_MODAL, OPEN_NEW_PIPELINE_MODAL, OPEN_EDIT_PIPELINE_MODAL, OPEN_ARCHIVE_TASK_MODAL, CLOSE_ARCHIVE_TASK_MODAL, CLOSE_USER_MODAL, OPEN_NEW_USER_MODAL, OPEN_EDIT_USER_MODAL, } from "../constants";
import { initialState } from "../providers/ModalContextProvider";
const closeModal = () => {
    return Object.assign({}, initialState);
};
const reducer = (state, action) => {
    switch (action.type) {
        case OPEN_NEW_TASK_MODAL: {
            const { targetStageId } = action.payload;
            return Object.assign(Object.assign({}, state), { taskModalOpen: true, targetStageId, taskToEdit: null });
        }
        case OPEN_EDIT_TASK_MODAL: {
            const { taskToEdit } = action.payload;
            return Object.assign(Object.assign({}, state), { taskModalOpen: true, taskToEdit, targetStageId: taskToEdit.stage_id });
        }
        case CLOSE_TASK_MODAL: {
            return closeModal();
        }
        case OPEN_NEW_STAGE_MODAL: {
            const { targetPipelineId } = action.payload;
            return Object.assign(Object.assign({}, state), { stageModalOpen: true, targetPipelineId });
        }
        case OPEN_STAGE_EDIT_MODAL: {
            const { stageToEdit } = action.payload;
            return Object.assign(Object.assign({}, state), { stageModalOpen: true, stageToEdit });
        }
        case CLOSE_STAGE_MODAL: {
            return closeModal();
        }
        case OPEN_NEW_PIPELINE_MODAL: {
            return Object.assign(Object.assign({}, state), { pipelineModalOpen: true, pipelineToEdit: null });
        }
        case OPEN_EDIT_PIPELINE_MODAL: {
            const { pipelineToEdit } = action.payload;
            return Object.assign(Object.assign({}, state), { pipelineModalOpen: true, pipelineToEdit });
        }
        case CLOSE_PIPELINE_MODAL: {
            return closeModal();
        }
        case OPEN_ARCHIVE_TASK_MODAL: {
            const archiveTask = action.payload;
            return Object.assign(Object.assign({}, state), { archiveTaskModalOpen: true, archiveModalTask: archiveTask });
        }
        case CLOSE_ARCHIVE_TASK_MODAL: {
            return closeModal();
        }
        case OPEN_NEW_USER_MODAL: {
            console.log("OPEN_NEW_USER_MODAL");
            return Object.assign(Object.assign({}, state), { userModalOpen: true });
        }
        case OPEN_EDIT_USER_MODAL: {
            const userToEdit = action.payload;
            return Object.assign(Object.assign({}, state), { userModalOpen: true, userToEdit });
        }
        case CLOSE_USER_MODAL: {
            return closeModal();
        }
        default:
            return state;
    }
};
export { reducer };
