import { PIPELINE_ADD_PIPELINE, PIPELINE_EDIT_PIPELINE, PIPELINE_SET_PRIMARY, PIPELINES_SET, } from "../constants";
const reducer = (state, action) => {
    switch (action.type) {
        case PIPELINES_SET: {
            const pipelines = action.payload;
            return Object.assign(Object.assign({}, state), { pipelines: pipelines.map((pipeline) => {
                    return Object.assign(Object.assign({}, pipeline), { is_primary: pipeline.is_primary === "1" });
                }) });
        }
        case PIPELINE_ADD_PIPELINE: {
            const pipeline = action.payload;
            return Object.assign(Object.assign({}, state), { pipelines: [...state.pipelines, Object.assign(Object.assign({}, pipeline), { is_primary: false })] });
        }
        case PIPELINE_SET_PRIMARY: {
            const primaryPipelineId = action.payload;
            return Object.assign(Object.assign({}, state), { pipelines: state.pipelines.map((pipeline) => (Object.assign(Object.assign({}, pipeline), { is_primary: pipeline.id === primaryPipelineId }))) });
        }
        case PIPELINE_EDIT_PIPELINE: {
            const pipeline = action.payload;
            return Object.assign(Object.assign({}, state), { pipelines: state.pipelines.map((p) => p.id === pipeline.id ? pipeline : p) });
        }
        default:
            return state;
    }
};
export { reducer };
