import { SET_ARCHIVE_FILTERED_PIPELINE, SET_ARCHIVE_SEARCH_VALUE, SET_ARCHIVE_TASKS, } from "../constants";
const reducer = (state, action) => {
    switch (action.type) {
        case SET_ARCHIVE_TASKS: {
            const archivedTasks = action.payload;
            return Object.assign(Object.assign({}, state), { archivedTasks });
        }
        case SET_ARCHIVE_SEARCH_VALUE: {
            const archiveSearchValue = action.payload;
            return Object.assign(Object.assign({}, state), { archiveSearchValue });
        }
        case SET_ARCHIVE_FILTERED_PIPELINE: {
            const archiveFilteredPipelineId = action.payload;
            return Object.assign(Object.assign({}, state), { archiveFilteredPipelineId });
        }
        default: {
            return state;
        }
    }
};
export { reducer };
